import Image from "next/image";

const testimonials = [
  {
    name: "Adrienne L.",
    title: "Student loan refinancing",
    text: "SoFi has made my refinancing experience social, interesting, and fun through their community events and member engagement.",
    image: "/img/client.jpg",
    color: "#F97316" // orange
  },
  {
    name: "Michael W.",
    title: "Student loan refinancing",
    text: "SoFi career coaching has helped me tremendously over the past few months. I doubt I would have been able to make the amount of progress in such a short time if I didn't have it available to me.",
    image: "/img/client.jpg",
    color: "#06B6D4" // cyan
  },
];

export default function Testimonials() {
  return (
    <div className="bg-transparent text-white py-16 px-4 sm:px-6 lg:px-8">
      <div className="max-w-5xl mx-auto text-center">
        <h2 className="text-3xl font-bold mb-4">Client testimonials</h2>
        <p className="text-sm text-gray-300 mb-12">
          This slide displays the glowing client testimonials that attest to the value of our products and services. It features client endorsements from business members.
        </p>

        <div className="flex flex-col md:flex-row items-start justify-center gap-12">
          {testimonials.map((testimonial, index) => (
            <div
              key={index}
              className="flex flex-col md:flex-row items-start gap-6 max-w-md text-left"
            >
              <div className="flex flex-col items-center">
                <div className="rounded-full border-4 border-white w-24 h-24 overflow-hidden">
                  <Image
                    src={testimonial.image}
                    alt={testimonial.name}
                    width={96}
                    height={96}
                    className="object-cover w-full h-full"
                  />
                </div>
                <div className="mt-4 text-center md:text-left">
                  <h3 className="text-base font-semibold" style={{ color: testimonial.color }}>{testimonial.name}</h3>
                  <p className="text-sm text-white mt-1">{testimonial.title}</p>
                </div>
              </div>
              <div className="relative bg-[#1F1F4B] text-white p-4 rounded-lg shadow-md flex-1">
                <div
                  className="absolute left-0 top-0 h-full w-1 rounded-s-md"
                  style={{ backgroundColor: testimonial.color }}
                ></div>
                <p className="text-sm leading-relaxed pl-2">"{testimonial.text}"</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
